}
.editMe-animation span {
  opacity: 1;
  animation: disappear 15s ease-in-out infinite;
}
@keyframes disappear {
  0% {
    opacity: 0;
  }
  79% {
    opacity: 0;
  }
  80% {
    opacity: 1;
  }
  85% {
    opacity: 1;
  }
  90% {
    opacity: 0;
  }
  100% {
    opacity: 0;
